import { IStackStyles, memoizeFunction, mergeStyleSets, IButtonStyles, IPersonaStyles } from "office-ui-fabric-react";

export interface IMahjongOverviewStyles {
    dashboard: IStackStyles;
    listItem: Partial<IButtonStyles>;
    persona: Partial<IPersonaStyles>;
    footerButton: Partial<IButtonStyles>;
}

export const getMahjongOverviewStyles = (): IMahjongOverviewStyles => ({
    dashboard: {
        root: { padding: "24px 0 0 0" },
    },
    listItem: {
        root: {
            maxWidth: "100%",
            width: "100%",
            padding: "0",
            margin: "10px 0",
        },
        flexContainer: {
            justifyContent: "flex-start",
        },
    },
    persona: {
        root: { margin: "15px 0 15px 15px" },
        tertiaryText: { whiteSpace: "normal" },
    },
    footerButton: {
        root: {
            marginTop: 30,
            float: "right",
            marginRight: 30,
        },
    },
});

export interface IMahjongOverviewClassNames {
    list: string;
    winnerIcon: string;
    winnerIcon2: string;
    eastIcon: string;
}

export const mahjongOverviewClassNames = memoizeFunction(
    (): IMahjongOverviewClassNames => {
        return mergeStyleSets({
            list: {
                margin: "0 10px",
            },
            winnerIcon: {
                position: "absolute",
                top: "5px",
                right: "5px",
                fontSize: 20,
                height: 20,
                width: 20,
                color: "green",
            },
            winnerIcon2: {
                position: "absolute",
                top: "5px",
                right: "30px",
                fontSize: 20,
                height: 20,
                width: 20,
                color: "green",
            },
            eastIcon: {
                position: "absolute",
                top: "5px",
                right: "5px",
                fontSize: 20,
                height: 20,
                width: 20,
            },
        });
    }
);
