export const loadData = (storageName: string) => {
    if (isSupported) {
        try {
            return JSON.parse(localStorage.getItem(storageName) || "");
        } catch (e) {
            return null;
        }
    }

    return null;
};

export const saveData = (storageName: string, data: any): boolean => {
    if (isSupported) {
        try {
            localStorage.setItem(storageName, JSON.stringify(data));
            return true;
        } catch (e) {
            return false;
        }
    }

    return false;
};

export const isSupported = (() => {
    try {
        let itemBackup = localStorage.getItem("");
        localStorage.removeItem("");
        localStorage.setItem("", itemBackup || "");
        if (itemBackup === null) localStorage.removeItem("");
        else localStorage.setItem("", itemBackup);
        return true;
    } catch (e) {
        return false;
    }
})();
