import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { AppThunk, RootState } from "../../../app/store";
import {
    IMahJongUser,
    IMahJongGame,
    createGame,
    createScore,
    IMahJongScore,
    IMahJongSet,
    MahJongTileTypes,
    MahJongSetTypes,
    getScore,
} from "../Mahjong.model";
import { isSupported, loadData, saveData } from "../../../utils/StorageUtil";
import { map, find, uniqBy, filter, without, forEach, sumBy, some, min } from "lodash";

interface MahjongOverviewState {
    players: IMahJongUser[];
    games: IMahJongGame[];
    currentPlayers: IMahJongUser[];
    currentGame: IMahJongGame;
}

const initialState: MahjongOverviewState = {
    players: [],
    games: [],
    currentPlayers: [],
    currentGame: createGame(),
};

export const overviewSlice = createSlice({
    name: "mahjongOverview",
    initialState,
    reducers: {
        loadFailed: (state) => {
            state = initialState;
        },
        loadSuccess: (state, action: PayloadAction<MahjongOverviewState>) => {
            state.players = action.payload?.players ?? initialState.players;
            state.games = action.payload?.games ?? initialState.games;
            state.currentPlayers = action.payload?.currentPlayers ?? initialState.currentPlayers;
            state.currentGame = action.payload?.currentGame ?? initialState.currentGame;
            updateGameUsers(state);
            updateCalculatedScore(state.currentGame);
        },
        setPlayers: (state, action: PayloadAction<IMahJongUser[]>) => {
            state.currentPlayers = action.payload;
            updateGameUsers(state);
        },
        addUser: (state, action: PayloadAction<IMahJongUser>) => {
            state.players = uniqBy(state.players, (user: IMahJongUser): string => user.name);
            state.players = uniqBy(state.players, (user: IMahJongUser): string => user.id);
            if (state.players && !find(state.players, (player: IMahJongUser) => player.name === action.payload.name)) {
                state.players.push(action.payload);
            }
            if (state.currentPlayers.length < 4 && !find(state.currentPlayers, (player: IMahJongUser) => player.id === action.payload.id)) {
                state.currentPlayers.push(action.payload);
            }
            updateGameUsers(state);
        },
        deleteUser: (state, action: PayloadAction<IMahJongUser>) => {
            if (state.players) {
                state.players = filter(state.players, (user: IMahJongUser): boolean => user.id !== action.payload.id);
                state.currentPlayers = filter(state.currentPlayers, (user: IMahJongUser): boolean => user.id !== action.payload.id);
                updateGameUsers(state);
            }
        },
        clearGame: (state) => {
            forEach(state.currentGame.score, (score: IMahJongScore) => {
                score.sets = [];
                score.points = 0;
                score.gameScore = 0;
                score.isWinner = false;
                score.isEast = false;
            });
            saveState(state);
        },
        clearAllGames: (state) => {
            forEach(state.currentGame.score, (score: IMahJongScore) => {
                score.sets = [];
                score.points = 0;
                score.gameScore = 0;
                score.isWinner = false;
                score.isEast = false;
            });
            state.games = [];
            saveState(state);
        },
        clearAll: (state) => {
            state = initialState;
            saveState(state);
        },
        updateScore: (state, action: PayloadAction<IMahJongScore>) => {
            const found = find(state.currentGame?.score, (score: IMahJongScore): boolean => score.playerId === action.payload?.playerId);
            if (found) {
                const otherScore = without<IMahJongScore>(state.currentGame.score, found);
                forEach(otherScore, (score: IMahJongScore) => {
                    if (action.payload.isWinner) {
                        score.isWinner = false;
                    }
                    if (action.payload.isEast) {
                        score.isEast = false;
                    }
                });
                state.currentGame.score = [...otherScore, { ...action.payload }];
                updateCalculatedScore(state.currentGame);
                saveState(state);
            }
        },
        saveGame: (state) => {
            updateCalculatedScore(state.currentGame);
            if (some(state.currentGame.score, (score: IMahJongScore): boolean => score.points !== 0)) {
                state.games = [...state.games, state.currentGame];
                state.currentGame = createGame();
                updateGameUsers(state);
                saveState(state);
            }
        },
    },
});

function updateGameUsers(state: MahjongOverviewState) {
    state.currentPlayers = filter(state.currentPlayers, (user: IMahJongUser): boolean => {
        return !!find(state.players, (player: IMahJongUser) => player.id === user.id);
    });
    state.currentPlayers = uniqBy(state.currentPlayers, (user: IMahJongUser): string => user.id);
    state.currentGame.score = map(
        state.currentPlayers,
        (user: IMahJongUser, index: number): IMahJongScore => {
            const found = find(state.currentGame.score, (score: IMahJongScore): boolean => score.playerId === user.id);
            return found || createScore(user.id);
        }
    );
    saveState(state);
}

function updateCalculatedScore(game: IMahJongGame): void {
    if (!game?.score || game.score.length <= 1) {
        return; // Nothing to update
    }

    let winnerFound = false;
    forEach(game.score, (score: IMahJongScore) => {
        score.points = scoreToPoints(score);
        winnerFound = winnerFound || !!score.isWinner;
    });

    if (!winnerFound) {
        return;
    }

    forEach(game.score, (scoreToCalculate: IMahJongScore) => {
        scoreToCalculate.gameScore = sumBy(game.score, (score: IMahJongScore): number => {
            if (score.playerId === scoreToCalculate.playerId) {
                return 0; // Skip
            }

            let difference = score.isWinner ? 0 : scoreToCalculate.points;
            if (!scoreToCalculate.isWinner) {
                difference -= score.points;
            }

            return score.isEast || scoreToCalculate.isEast ? difference * 2 : difference;
        });
    });
}

export const DefaultWinnerPoints = 10;

export function scoreToPoints(score: IMahJongScore): number {
    let points = score.isWinner ? DefaultWinnerPoints : 0;
    points += sumBy(score.sets, setToPoints);

    let honorFactor = 1;
    if (
        some(score.sets, (set: IMahJongSet): boolean => {
            return (
                !!set.ownWind &&
                set.tileType === MahJongTileTypes.Wind &&
                (set.setType === MahJongSetTypes.Poong || set.setType === MahJongSetTypes.Kong)
            );
        })
    ) {
        // Poong or Kong in own wind
        honorFactor *= 2;
    }

    if (
        some(score.sets, (set: IMahJongSet): boolean => {
            return set.tileType === MahJongTileTypes.Dragon && (set.setType === MahJongSetTypes.Poong || set.setType === MahJongSetTypes.Kong);
        })
    ) {
        // Poong or Kong in dragons
        honorFactor *= 2;
    }

    if (
        some(score.sets, (set: IMahJongSet): boolean => {
            return set.tileType === MahJongTileTypes.Flower && set.setType === MahJongSetTypes.Kong;
        })
    ) {
        // Kong in flowers doubles twice
        honorFactor *= 4;
    }

    if (score.isWinner) {
        let containDragonsOrWinds = false;
        let containBamboo = 0;
        let containCircle = 0;
        let containCharacter = 0;
        forEach(score.sets, (set: IMahJongSet) => {
            if (set.tileType === MahJongTileTypes.Bamboo) {
                containBamboo = 1;
            }
            if (set.tileType === MahJongTileTypes.Circle) {
                containCircle = 1;
            }
            if (set.tileType === MahJongTileTypes.Character) {
                containCharacter = 1;
            }
            if (set.tileType === MahJongTileTypes.Dragon || set.tileType === MahJongTileTypes.Wind) {
                containDragonsOrWinds = true;
            }
        });

        if (containBamboo + containCircle + containCharacter === 1) {
            if (containDragonsOrWinds) {
                honorFactor *= 2;
            } else {
                honorFactor *= 8;
            }
        }
    }

    return min([points * honorFactor, 300]) ?? 0;
}

export function setToPoints(set: IMahJongSet): number {
    if (set.tileType === MahJongTileTypes.Flower) {
        switch (set.setType) {
            // Each flower tile yields 4 points
            case MahJongSetTypes.Pair:
                return 8;
            case MahJongSetTypes.Chow:
            case MahJongSetTypes.Poong:
                return 12;
            case MahJongSetTypes.Kong:
                return 16;
            case MahJongSetTypes.Single:
            default:
                return 4;
        }
    }

    // Special end pair rule:
    if (
        set.setType === MahJongSetTypes.Pair &&
        (set.tileType === MahJongTileTypes.Dragon || (set.tileType === MahJongTileTypes.Wind && set.ownWind))
    ) {
        return 2;
    }

    // All other tile types:
    let sub = 0;
    if (set.setType === MahJongSetTypes.Poong) {
        sub = 2;
    } else if (set.setType === MahJongSetTypes.Kong) {
        sub = 8;
    }
    if (set.oneOrNine || set.tileType === MahJongTileTypes.Dragon || set.tileType === MahJongTileTypes.Wind) {
        sub *= 2;
    }
    if (set.inHand) {
        sub *= 2;
    }

    return sub;
}

export interface IMahJongHistory {
    playerId: string;
    sum: number;
    oldGameScore: IMahJongScore[];
}

export function history(games: IMahJongGame[], players: IMahJongUser[]): IMahJongHistory[] {
    const uniquePlayers = uniqBy(players, (user: IMahJongUser): string => user.id);

    const gameHistory = filter(games, (game: IMahJongGame): boolean => {
        const uniqueScores = uniqBy(game.score, (score: IMahJongScore): string => score.playerId);
        if (uniquePlayers.length !== uniqueScores.length) {
            return false;
        }
        return !some(uniquePlayers, (player: IMahJongUser) => !find(uniqueScores, (score: IMahJongScore) => score.playerId === player.id));
    });

    if (gameHistory.length === 0) {
        return [];
    }

    return map(
        uniquePlayers,
        (user: IMahJongUser): IMahJongHistory => {
            const oldGameScore = map(gameHistory, (game: IMahJongGame): IMahJongScore => getScore(game, user));
            return {
                playerId: user.id,
                sum: sumBy(oldGameScore, (score: IMahJongScore): number => {
                    return score?.gameScore ?? 0;
                }),
                oldGameScore,
            };
        }
    );
}

const { loadFailed, loadSuccess } = overviewSlice.actions;
export const { setPlayers, addUser, deleteUser, clearGame, clearAllGames, clearAll, updateScore, saveGame } = overviewSlice.actions;

const storageName = "Mahjong";

function saveState(state: MahjongOverviewState) {
    saveData(storageName, state);
}

export const load = (): AppThunk => (dispatch) => {
    if (isSupported) {
        dispatch(loadSuccess(loadData(storageName) as MahjongOverviewState));
    } else {
        dispatch(loadFailed());
    }
};

export const selectPlayers = (state: RootState) => state.mahjongOverview.players || [];
export const selectGames = (state: RootState) => state.mahjongOverview.games || [];
export const selectCurrentGame = (state: RootState) => state.mahjongOverview.currentGame || createGame();
export const selectCurrentPlayers = (state: RootState) => state.mahjongOverview.currentPlayers || [];

export default overviewSlice.reducer;
