import {
    DragonLabel,
    WindLabel,
    BambooLabel,
    CircleLabel,
    CharacterLabel,
    FlowerLabel,
    UnknownLabel,
    SingleLabel,
    PairLabel,
    ChowLabel,
    PoongLabel,
    KongLabel,
} from "../../localization/DisplayStrings";
import MahJongBack from "../../images/MahjongBack.jpg";
import MahJongWindDiscs from "../../images/MahjongWindDiscs.jpg";
import MahJongGreenDragon from "../../images/MahjongGreenDragon.jpg";
import MahJongEast from "../../images/MahjongEast.jpg";
import MahJongBamboo1 from "../../images/MahjongBamboo1.jpg";
import MahJongBamboo6 from "../../images/MahjongBamboo6.jpg";
import MahJongBambooChow from "../../images/MahjongBambooChow.jpg";
import MahJongCircle1 from "../../images/MahjongCircle1.jpg";
import MahJongCircle5 from "../../images/MahjongCircle5.jpg";
import MahJongCircleChow from "../../images/MahjongCircleChow.jpg";
import MahJongCharacter1 from "../../images/MahjongCharacter1.jpg";
import MahJongCharacter4 from "../../images/MahjongCharacter4.jpg";
import MahJongCharacterChow from "../../images/MahjongCharacterChow.jpg";
import MahJongSeason1 from "../../images/MahjongSeason1.jpg";
import MahJongSeason2 from "../../images/MahjongSeason2.jpg";
import MahJongSeason3 from "../../images/MahjongSeason3.jpg";
import MahJongSeason4 from "../../images/MahjongSeason4.jpg";
import MahJongFlower2 from "../../images/MahjongFlower2.jpg";
import { MahJongTileTypes, MahJongSetTypes, IMahJongSet } from "./Mahjong.model";

export function displayTileType(tile: MahJongTileTypes) {
    switch (tile) {
        case MahJongTileTypes.Dragon:
            return DragonLabel();
        case MahJongTileTypes.Wind:
            return WindLabel();
        case MahJongTileTypes.Bamboo:
            return BambooLabel();
        case MahJongTileTypes.Circle:
            return CircleLabel();
        case MahJongTileTypes.Character:
            return CharacterLabel();
        case MahJongTileTypes.Flower:
            return FlowerLabel();
        default:
            return UnknownLabel();
    }
}

function imageTileBack() {
    return MahJongBack;
}

export function imageTileType(tile: MahJongTileTypes, index?: number) {
    switch (tile) {
        case MahJongTileTypes.Dragon:
            return MahJongGreenDragon;
        case MahJongTileTypes.Wind:
            return MahJongEast;
        case MahJongTileTypes.Bamboo:
            if (index === 1) {
                return MahJongBamboo1;
            }
            return MahJongBamboo6;
        case MahJongTileTypes.Circle:
            if (index === 1) {
                return MahJongCircle1;
            }
            return MahJongCircle5;
        case MahJongTileTypes.Character:
            if (index === 1) {
                return MahJongCharacter1;
            }
            return MahJongCharacter4;
        case MahJongTileTypes.Flower:
            if (index === 1) {
                return MahJongSeason1;
            }
            if (index === 2) {
                return MahJongSeason2;
            }
            if (index === 3) {
                return MahJongSeason3;
            }
            if (index === 6) {
                return MahJongFlower2;
            }
            return MahJongSeason4;
        default:
            return undefined;
    }
}

export function imageSet(item?: IMahJongSet) {
    if (!item) {
        return [];
    }

    if (item.tileType === MahJongTileTypes.Flower) {
        return getFlowerImageSet(item.setType);
    }

    if (item.tileType === MahJongTileTypes.Wind) {
        return getWindImageSet(item);
    }

    return getOtherImageSet(item);
}

function getFlowerImageSet(setType: MahJongSetTypes) {
    switch (setType) {
        case MahJongSetTypes.Single:
            return [imageTileType(MahJongTileTypes.Flower)];
        case MahJongSetTypes.Pair:
            return [imageTileType(MahJongTileTypes.Flower, 6), imageTileType(MahJongTileTypes.Flower, 3)];
        case MahJongSetTypes.Chow:
            return [imageTileType(MahJongTileTypes.Flower, 1), imageTileType(MahJongTileTypes.Flower, 2), imageTileType(MahJongTileTypes.Flower, 6)];
        case MahJongSetTypes.Poong:
            return [imageTileType(MahJongTileTypes.Flower, 1), imageTileType(MahJongTileTypes.Flower, 2), imageTileType(MahJongTileTypes.Flower, 4)];
        case MahJongSetTypes.Kong:
            return [
                imageTileType(MahJongTileTypes.Flower, 1),
                imageTileType(MahJongTileTypes.Flower, 2),
                imageTileType(MahJongTileTypes.Flower, 3),
                imageTileType(MahJongTileTypes.Flower, 4),
            ];
        default:
            return [];
    }
}

function getWindImageSet(item: IMahJongSet) {
    const imageList = getOtherImageSet(item);
    if (item.ownWind) {
        return [...imageList, MahJongWindDiscs];
    }

    return imageList;
}

function getOtherImageSet(item: IMahJongSet) {
    const image = imageTileType(item.tileType, item.oneOrNine ? 1 : undefined);

    if (item.inHand) {
        switch (item.setType) {
            case MahJongSetTypes.Poong:
                return [image, imageTileBack(), image];
            case MahJongSetTypes.Kong:
                return [image, imageTileBack(), image, imageTileBack()];
            default:
                break;
        }
    }

    switch (item.setType) {
        case MahJongSetTypes.Pair:
            return [image, image];
        case MahJongSetTypes.Chow:
            return imageChowType(item.tileType);
        case MahJongSetTypes.Poong:
            return [image, image, image];
        case MahJongSetTypes.Kong:
            return [image, image, image, image];
        default:
            return [];
    }
}

function imageChowType(tile: MahJongTileTypes) {
    switch (tile) {
        case MahJongTileTypes.Bamboo:
            return [MahJongBambooChow];
        case MahJongTileTypes.Circle:
            return [MahJongCircleChow];
        case MahJongTileTypes.Character:
            return [MahJongCharacterChow];
        default:
            return [];
    }
}

export function displaySetType(set: MahJongSetTypes) {
    switch (set) {
        case MahJongSetTypes.Single:
            return SingleLabel();
        case MahJongSetTypes.Pair:
            return PairLabel();
        case MahJongSetTypes.Chow:
            return ChowLabel();
        case MahJongSetTypes.Poong:
            return PoongLabel();
        case MahJongSetTypes.Kong:
            return KongLabel();
        default:
            return UnknownLabel();
    }
}
